import { Alert } from '@mui/material'
import { Component } from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import {MovesList} from '../cmps/MovesList'
import { TransferFund } from '../cmps/TransferFund'
import contactService from '../services/contactService'
import { userService } from '../services/userService'
import { getContactById, removeContact } from '../store/actions/contactAction'
import { addMove } from '../store/actions/userAction'

class _ContactDetails extends Component {

    state = {
        contact: null,
        msg: ''
    }

    componentDidMount() {
        this.loadContact()
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.match.params.id !== this.props.match.params.id) {
            this.props.getContactById(this.props.match.params.id)
        }
    }

    async loadContact() {
        const contact = await this.props.getContactById(this.props.match.params.id)
        this.setState({ contact })
    }

    onGoBack = () => {
        this.props.history.push('/contact')
    }

    onTransferCoins = (val) => {
        this.setState({ msg: '' })
        const { contact } = this.state
        this.props.addMove({
            toId: contact._id,
            to: contact.name,
            at: Date.now(),
            amount: val
        })
    }
    noBalance = () => {
        this.setState({ msg: `You do not have enough, try up to ${this.props.user.coins}` })
    }
    render() {
        const { contact, msg } = this.state
        if (!contact) return <div>Loading...</div>
        return (
            <div className="container contact-details">
                <div className='left'>
                    <div className='actions'>
                        <button onClick={this.onGoBack}>Back</button>
                        <Link to={`/contact/edit/${contact._id}`}>Edit</Link>
                    </div>
                    <div>
                        <img src={contact.imgUrl} alt="" />
                    </div>
                    <div>
                        <div className="details-row">
                            <span className="material-icons icon">badge</span>
                            <h2>Name:</h2>
                            <h3>{contact.name}</h3>
                        </div>
                        <hr />
                        <div className="details-row">
                            <span className="material-icons icon">call</span>
                            <h2>Phone:</h2>
                            <h3>{contact.phone}</h3>
                        </div>
                        <hr />
                        <div className="details-row">
                            <span className="material-icons icon">email</span>
                            <h2>Email:</h2>
                            <h3>{contact.email}</h3>
                        </div>
                    </div>
                </div>
                <div className='right'>

                    {/* <Alert severity="error">You do not have enough, try up to {}</Alert> */}
                    {msg && <Alert severity="error">{msg}</Alert>}
                    <TransferFund noBalance={this.noBalance} contact={contact} maxCoins={this.props.user.coins} onTransferCoins={this.onTransferCoins} />
                    <MovesList moves={this.props.user.moves.filter(move => move.toId === contact._id)} />
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        user: state.userModule.loggedInUser,
    }
}

const mapDispatchToProps = {
    addMove,
    getContactById
}

export const ContactDetails = connect(mapStateToProps, mapDispatchToProps)(_ContactDetails)







