import { Component } from 'react'
import { connect } from 'react-redux'
import { setFilterBy } from '../store/actions/contactAction'

 class _ContactFilter extends Component {
    state={
        term:''
    }

    handleChange = ({ target }) => {
        // const field = target.name
        const value = target.type === 'number' ? +target.value : target.value
        // this.setState({ [field]: value }, () => {
            this.props.setFilterBy({term:value})
        // })

    }

    render() {
        const { term  } = this.state
        return (
            <div className='contact-filter'>
                <form >
                <section className='input-container'>
                    <label htmlFor="term">Name / Phone</label>
                    <input onChange={this.handleChange} value={term} type="text" name="term" id="term" />
                </section>
                </form>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        filterBy:state.contactModule.filterBy
    }
}

const mapDispatchToProps = {
    setFilterBy,
}

export const ContactFilter = connect(mapStateToProps, mapDispatchToProps)(_ContactFilter)
