import { storageService } from "./storageService"

export const userService = {
    getUser,
    addMove,
    signup
}

function getUser() {
    if (storageService.load('currUser')) {
        return Promise.resolve(storageService.load('currUser'))
    } return null
}

function addMove(move) {
    var user = storageService.load('currUser')
    user.moves.unshift(move)
    user.coins -= move.amount
    storageService.store('currUser', user)
    return user
}

function signup(name) {
    const newUser = {
        name,
        coins: 100,
        moves: []
    }
    storageService.store('currUser', newUser)
}