import { Component, createRef } from 'react'
import { connect } from 'react-redux'
import contactService from '../services/contactService'
import { getContactById, removeContact } from '../store/actions/contactAction'

 class _ContactEdit extends Component {
    state = {
        contact: null
    }
    inputRef = createRef()

    async componentDidMount() {
        const contactId = this.props.match.params.id
        this.setState({contact:contactId? await this.props.getContactById(contactId):contactService.getEmptyContact()})
    }

    handleChange = ({ target }) => {
        const field = target.name
        const value = target.type === 'number' ? +target.value : target.value
        if (field === 'name') {
            this.setState(prevState => ({ contact: { ...prevState.contact, [field]: value, imgUrl: `https://robohash.org/${value}?set=set5` } }))
        } else {
            this.setState(prevState => ({ contact: { ...prevState.contact, [field]: value } }))
        }

    }
    async onRemoveContact(id) {
        await this.props.removeContact(id)
        this.props.history.push('/contact')
    }
    removeBtn = () => {
        const { contact } = this.state
        if (contact._id) {
            return (
                <button onClick={() => this.onRemoveContact(contact._id)}><span className="material-icons icon">delete</span></button>
            )
        }
    }
    onSaveContact = async (ev) => {
        ev.preventDefault()
        await contactService.saveContact({ ...this.state.contact })
        this.props.history.push('/contact')
    }
    render() {
        const { contact } = this.state
        if (!contact) return <div>Loading...</div>
        return (
            <div className='contact-edit'>
                <h1>{contact._id ? 'Edit' : 'Add'} Contact</h1>
                <div className='main'>
                    <img src={contact.imgUrl} alt="" />
                    <div className='data'>
                        {this.removeBtn()}
                        <form onSubmit={this.onSaveContact} >
                            <label htmlFor="name">Nmae</label>
                            <input ref={this.inputRef} onChange={this.handleChange} value={contact.name} type="text" name="name" id="name" />
                            <label htmlFor="phone">Phone</label>
                            <input ref={this.inputRef} onChange={this.handleChange} value={contact.phone} type="text" name="phone" id="phone" />
                            <label htmlFor="email">Email</label>
                            <input onChange={this.handleChange} value={contact.email} type="text" name="email" id="email" />
                            <button >Save</button>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
    }
}

const mapDispatchToProps = {
    removeContact,
    getContactById

}

export const ContactEdit = connect(mapStateToProps, mapDispatchToProps)(_ContactEdit)
