import { Alert } from "@mui/material";
import { createRef } from "react"


export function TransferFund({ contact, maxCoins, onTransferCoins,noBalance }) {

    const inputRef = createRef()
    var msg = ''
    function checkCoins(ev) {
        ev.preventDefault()
        if (maxCoins - inputRef.current.value < 0) {
            noBalance()
        } else {
            onTransferCoins(inputRef.current.value)
            inputRef.current.value = ''
        }
    }
    return (
        <div>
            {msg?msg:''}
            <h3>Transfer coins to {contact.name}</h3>
            <form onSubmit={checkCoins}>
                <input type="number" ref={inputRef} />
                <button>Transfer</button>
            </form>
        </div>
    )
}
