import React from 'react'

export function MovesList({ moves }) {
    console.log(moves);
    return (
        
            <ul className='move-list'>
                {moves.map(move => 
                <li key={move.at} className='move-preview'>
                    <h4>{new Date(move.at).toLocaleString()}</h4>
                    <h3><b>move {move.amount} dollars</b></h3>
                </li>)}
            </ul>
        
    )
}
