const INITIAL_STATE = {
    loggedInUser: null
}

export function userReducer(state = INITIAL_STATE, action) {
console.log(action.user);
    switch (action.type) {
        case 'LOAD_USER':
            return {
                ...state,
                loggedInUser: { ...action.user }
            }
        // case 'UPDATE_USER':
        //     return {
        //         ...state,
        //         loggedInUser: {...action.user}
        //     }
        // case 'SPEND_BALANCE':
        //     const { loggedInUser } = state
        //     return {
        //         ...state,
        //         loggedInUser: { ...loggedInUser, balance: loggedInUser.balance - action.amount }
        //     }

        default:
            return state;
    }
}